body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.NoResults {
  padding: 5px;
  color: #9e9e9e;
}
.Results {
  padding: 5px;
}
.Results strong {
  padding: 5px;
  color: #9c27b0;
}

.ResultsButton {
  padding: 5px 20px;
  background-color: #9c27b0;
  color: white;
  border: none;
  border-radius: 5px;
  margin: 5px 0 5px 5px;
}
.ResultsButton:hover {
  cursor: pointer;
  background-color: #ab47bc;
}

.TestRunnerHeader {
  margin-bottom: 5px;
}
.TestRunnerHeader strong {
  padding: 5px;
  color: #9c27b0;
}

.TestRunnerHeader .version {
  color: #9e9e9e;
}

.TestRunnerHeader button {
  padding: 5px 20px;
  background-color: #9c27b0;
  color: white;
  border: none;
  border-radius: 5px;
  float: right;
}
.TestRunnerHeader button:hover {
  cursor: pointer;
  background-color: #ab47bc;
}

.TestRunnerDesc {
  font-size: 16px;
  padding: 5px;
  color: #ba68c8;
}

.TestRunner span {
  padding: 5px;
}

.TestRunner input {
  padding: 2px;
  width: 50px;
  text-align: right;
  border: none;
  border-bottom: 1px solid #9c27b0;
  color: #9c27b0;
}

.Home {
  font-family: "Anonymous Pro", monospace;
  font-size: 24px;
  margin: 0 auto;
  width: 1100px;
}

.Home h1 {
  color: #4a148c;
}

.Home h4 {
  color: #9c27b0;
}

.Home p,
.Home a {
  color: #757575;
}

.Home a:hover {
  color: #9c27b0;
}

input,
button {
  font-family: inherit;
  font-size: inherit;
}

.Settings {
  padding: 20px;
  border: 10px double #9c27b0;
  margin-bottom: 20px;
}

.Settings input {
  margin-left: 10px;
  padding: 2px;
  width: 75px;
  text-align: right;
  border: none;
  border-bottom: 1px solid #9c27b0;
  color: #9c27b0;
}

.SettingsHeader {
  padding: 0 10px;
  background-color: white;
  margin-top: -38px;
  position: absolute;
}

.LibBlock {
  padding: 20px;
  border: 10px double #9c27b0;
  margin-bottom: 20px;
}

.Footer {
  font-size: 16px;
  margin: 100px 0 20px 0;
  text-align: center;
}

.Footer .version {
  margin-top: 20px;
  font-size: 14px;
  color: #757575;
}

.search-box {
  display: inline-block;
  padding: 5px;
}
.search-box input {
  padding: 2px 5px;
  border: none;
}
.search-box button {
  padding: 5px 10px;
  border: none;

  border-radius: 5px;
  margin-left: 5px;
}

.search-box button:hover {
  cursor: pointer;
}

/* Themes go here: */
.search-box.theme-variant-purple span {
  color: #9c27b0;
}
.search-box.theme-variant-purple input {
  color: #ba68c8;
  background-color: #e1bee7;
}
.search-box.theme-variant-purple button {
  color: white;
  background-color: #9c27b0;
}
.search-box.theme-variant-purple button:hover {
  background-color: #ab47bc;
}

.probe {
  -webkit-animation: probe-animation 1s;
          animation: probe-animation 1s;
}

@-webkit-keyframes probe-animation {
  from {
    color: red;
  }
  to {
    color: blue;
  }
}

@keyframes probe-animation {
  from {
    color: red;
  }
  to {
    color: blue;
  }
}

.search-box {
  display: inline-block;
  padding: 5px;
}
.search-box input {
  padding: 2px 5px;
  border: none;
}
.search-box button {
  padding: 5px 10px;
  border: none;

  border-radius: 5px;
  margin-left: 5px;
}

.search-box button:hover {
  cursor: pointer;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

